import React from 'react'
import { Container, Jumbotron } from 'reactstrap'

const PageHeadline = ({ pagename, pageheadline }) => {
    return (
        <Container fluid className="custom-font text-center">
            <Jumbotron>
                <h1> {pagename}</h1>
                <h2> {pageheadline} </h2>
            </Jumbotron>
        </Container>
    )
}

const BlogHeadline = ({ blogTitle, blogAuthor, blogDate }) => {
    return (
        <Container fluid className="custom-font">
            <Jumbotron>
                <h1>{blogTitle}</h1>
                <h2 className="blog-date">{blogDate}</h2>
                <h2 className="blog-author">{blogAuthor}</h2>
            </Jumbotron>
        </Container>
    )
}

