import React from 'react'
import ArticlePreview from '../components/article-preview/article-preview'
// import PostLinkCard from '../components/post-link-card'
import { graphql } from 'gatsby'
import NavbarContent from '../components/navigation/navbar'
import { PageHeadline } from '../components/headline'
import styles from './blog.module.css'
import { Helmet } from 'react-helmet'

const ProjectListing = (props) => {
    const posts = props.data.allContentfulProjects.edges
    return (
        <React.Fragment>
            <Helmet title={'iD8 - Projects'} />
            <NavbarContent />
            <div className={styles.hero}>Our Projects</div>
            <div className="wrapper">
                <h2 className="section-headline">Recent articles</h2>
                <ul className="article-list">
                    {posts.map(({ node }) => {
                        return (
                            <li key={node.slug}>
                                <ArticlePreview
                                    category="projects"
                                    article={node}
                                />
                            </li>
                        )
                    })}
                </ul>
            </div>
        </React.Fragment>
    )
}

export const query = graphql`
    query PreviewQuery {
        allContentfulProjects {
            edges {
                node {
                    title
                    tags
                    slug
                    heroImage {
                        fluid(
                            maxWidth: 350
                            maxHeight: 196
                            resizingBehavior: SCALE
                        ) {
                            ...GatsbyContentfulFluid_tracedSVG
                        }
                    }
                    description {
                        childMarkdownRemark {
                            html
                        }
                    }
                }
            }
        }
    }
`

export default ProjectListing
